.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: #ffb8ca;
}

.header__logo {
  height: 50px;
}

.header__list {
  display: none;

  @media screen and (min-width: 1200px) {
    display: flex;
    gap: 35px;
  }
}

.header__link {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  color: #fff;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    color: #d41443;
  }
}

.header__button {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    padding: 11.5px 37px;

    cursor: pointer;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;

    border: none;
    border-radius: 22px;

    color: #d41443;
  }
}

.button__img {
  padding-left: 12px;

  background-color: #d41443;
}
